body {
    margin: 0;
}

.app {
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: dimgray;
    height: 100vh;
    width: 100vw;
}

.color-picker {
    background-color: white;
    height: 750px;
    width: 550px;
    border-radius: 30px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.flex-center {
    display: flex;
    justify-content: center;
}

.complementary-colors {
    display: flex;
    justify-content: space-between;
    padding: 5%;
    flex-grow: 0.5;
    align-items: center;
    border-radius: 70% 70% 30px 30px
}

.complementary-color {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    margin-top: 5%;
}

.brightness-slider {
    align-items: center;
}

.bulb-icon {
    font-size: 30px;
    margin: 2%;
}

.shadowed {
    box-shadow: 7px 7px 30px gray;
}

.color-wheel {
    margin: 5%;
}

.link-icon {
    display: flex;
    justify-content: flex-end;
    margin-top: 2%;
    margin-right: 2%;
    font-size: 18px;
}

i {
    cursor: pointer;
}
